<template>
  <div>
    <!-- Breadcrumb -->
    <div class="d-flex justify-content-start align-items-center">
      <b-button
        class="mr-1"
        variant="outline-primary"
        style="padding: 5px 10px"
        :to="{ name: 'curso-taller-deportivo', params: { id: $router.currentRoute.params.tallerGlobal_id } }"
      >
        <feather-icon
          icon="ChevronsLeftIcon"
        />
        volver
      </b-button>
      <span class="border-right pr-1 mr-1"><h3>Deportivo</h3></span>
      <feather-icon
        icon="HomeIcon"
        class="text-primary"
        size="16"
      />
      <feather-icon
        icon="ChevronRightIcon"
      />
      <b-breadcrumb
        :items="itemsBreadcrumb"
      />
    </div>
    <br>
    <!-- Breadcrumb End -->
    <b-card class="mb-1 pl-1">
      <b-row>
        <b-col
          lg="7"
          md="7"
          sm="12"
        >
          <h5 class="">
            <feather-icon
              icon="PlayIcon"
            />
            <strong class="text-primary">TALLER DEPORTIVO : </strong>{{ nombre_taller }}
            ( <b-badge
              :variant="(estado_taller == 1) ? 'light-success' : 'light-danger'"
              class="px-2"
            >
              {{ (estado_taller == 1) ? 'ABIERTO' : 'CERRADO' }}
            </b-badge> )
          </h5>
          <h5>
            <feather-icon
              icon="PlayIcon"
            />
            <strong class="text-primary">SEMESTRE : </strong>{{ nombre_taller }}
          </h5>

        </b-col>
        <b-col
          lg="5"
          md="5"
          sm="12"
        >
          <h5 class="">
            <feather-icon
              icon="PlayIcon"
            />
            <strong class="text-primary">DOCENTE : </strong>{{ nombre_taller }}
          </h5>

        </b-col>
      </b-row>
    </b-card>
    <b-card
      fluid
      class="pb-2 px-1"
    >
      <b-row>
        <b-col cols="6">
          <h5 class="">
            <feather-icon
              icon="PlayIcon"
            />
            <strong class="text-primary">ACTIVIDADES : </strong>
          </h5>
        </b-col>
        <b-col cols="6">
          <b-button
            size="sm"
            variant="outline-primary"
            class="mb-1 float-right px-3"
            :disabled="estado_taller == 1 ? false : true"
            :to="{ name: 'taller-actividad-deportivo', params: { id: 0 , taller_id : id }}"
          >
            <feather-icon icon="PlusCircleIcon" /> Registrar nueva actividad
          </b-button>
        </b-col>

      </b-row>
      <br>
      <!-- User Interface controls -->
      <!-- Main table element -->
      <b-row>
        <b-col
          cols="4"
        >
          <label>Mostrar</label>
          <b-form-select
            id="per-page-select"
            v-model="perPage"
            :options="pageOptions"
            size="sm"
            class="per-page-selector d-inline-block mx-50"
            style="width:auto"
            @change="actualizar_to_from"
          />
          <label>registros</label>
        </b-col>
        <b-col
          cols="4"
        />
        <b-col
          cols="4"
          class="pb-1"
        >
          <b-form-group
            class="mb-0"
          >
            <b-input-group>
              <b-form-input
                id="filter-input"
                v-model="filter"
                type="search"
                placeholder="Buscar..."
                size="sm"
              />

              <b-input-group-append>
                <b-button
                  :disabled="!filter"
                  variant="success"
                  size="sm"
                  @click="filter = ''"
                >
                  Limpiar
                </b-button>
              </b-input-group-append>
            </b-input-group>
          </b-form-group>
        </b-col>
      </b-row>
      <b-table
        :items="actividadesTallerDeportivo"
        :fields="fields"
        :current-page="currentPage"
        :per-page="perPage"
        :filter="filter"
        :filter-included-fields="filterOn"
        :sort-by.sync="sortBy"
        :sort-desc.sync="sortDesc"
        :sort-direction="sortDirection"
        stacked="sm"
        head-variant="light"
        class="position-relative"
        show-empty
        small
        hover
        responsive
        :empty-text="'No hay actividades para mostrar'"
        :empty-filtered-text="'No hay actividades que coincidan con su solicitud.'"
        @filtered="onFiltered"
      >
        <template #cell(acciones)="row">
          <b-dropdown
            variant="link"
            no-caret
            :right="$store.state.appConfig.isRTL"
          >
            <template #button-content>
              <feather-icon
                icon="MoreVerticalIcon"
                size="16"
                class="align-middle text-body"
              />
            </template>
            <b-dropdown-item
              :to="{ name: 'taller-actividad-deportivo', params: { id: row.item.id, taller_id : id } }"
            >
              <feather-icon icon="EditIcon" />
              <span class="align-middle ml-50">Editar</span>
            </b-dropdown-item>

            <b-dropdown-item
              @click="eliminarActividad(row.item)"
            >
              <feather-icon icon="TrashIcon" />
              <span class="align-middle ml-50">Eliminar</span>
            </b-dropdown-item>
          </b-dropdown>
        </template>
      </b-table>
      <b-row
        cols="12"
        class="d-flex justify-content-between mb-1 mb-md-0"
      >
        <b-col cols="6">
          <span class="text-muted">Mostrando registros del {{ from }} al {{ to }} de un total de {{ totalRows }} registros</span>
        </b-col>
        <b-col cols="4">
          <b-pagination
            v-model="currentPage"
            :total-rows="totalRows"
            :per-page="perPage"
            align="fill"
            size="sm"
            class="my-0"
            @input="actualizar_to_from"
          />
        </b-col>
      </b-row>
    </b-card>
  </div>

</template>

<script>
import {
  BCard, BDropdown, BBreadcrumb, BDropdownItem, BBadge, BFormSelect, BButton, BFormGroup, BFormInput, BRow, BCol, BInputGroup, BPagination, BTable, BInputGroupAppend,
} from 'bootstrap-vue'
import store from '@/store'

import { onUnmounted } from '@vue/composition-api'
import { mapState } from 'vuex'
import actDeportivasTallerStoreModule from './actDeportivasTallerStoreModule'

export default {

  components: {
    BButton,
    BDropdown,
    BDropdownItem,
    BFormGroup,
    BFormInput,
    BCard,
    BRow,
    BCol,
    BInputGroup,
    BFormSelect,
    BPagination,
    BTable,
    BInputGroupAppend,
    BBadge,
    BBreadcrumb,
  },

  data() {
    return {
      id: this.$attrs.id,
      tallerGlobal_id: this.$attrs.tallerGlobal_id,
      items: [],
      nombre_taller: '',
      estado_taller: '',
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15, { value: 100, text: 'Ver todo' }],
      sortBy: '',
      sortDesc: false,
      sortDirection: 'asc',
      filter: null,
      filterOn: [],
      to: 0,
      from: 0,
      totalRows: 0,

      itemsBreadcrumb: [
        {
          text: 'Talleres Globales',
          to: { name: 'servicio-deportivo' },
        },
        {
          text: 'Talleres',
          to: { name: 'curso-taller-deportivo', params: { id: this.$router.currentRoute.params.tallerGlobal_id } },
        },
        {
          text: 'Actividades',
          active: true,
        },
      ],
    }
  },
  computed: {
    ...mapState('actividades_taller', ['actividadesTallerDeportivo']),
  },
  mounted() {
    store.state.actividades_taller.curso_taller_id = this.id
    store.state.actividades_taller.tallerGlobal_id = this.tallerGlobal_id
    store.dispatch('actividades_taller/fetchActividades').then(() => {
      if (store.state.actividades_taller.actividadesTallerDeportivo !== undefined) {
        this.totalRows = store.state.actividades_taller.actividadesTallerDeportivo.length
      }

      this.actualizar_to_from()
    })
    store.dispatch('actividades_taller/fetchCursoTaller').then(response => {
      this.nombre_taller = response.data.data.nombre
      this.estado_taller = response.data.data.estado
    })
  },
  methods: {

    actualizar_to_from() {
      const { totalRows } = this
      if (totalRows === 0) {
        this.from = 0
        this.to = 0
      } else
      if (this.currentPage === 1) {
        this.from = 1
        if (totalRows <= this.perPage) {
          this.to = totalRows
        } else {
          this.to = this.perPage
        }
      } else {
        this.from = ((this.currentPage - 1) * this.perPage) + 1
        if ((this.currentPage * this.perPage) <= totalRows) {
          this.to = this.currentPage * this.perPage
        } else {
          this.to = totalRows
        }
      }
    },
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length
      this.currentPage = 1
      this.actualizar_to_from()
    },
    eliminarActividad(value) {
      this.$swal({
        title: `Esta seguro de eliminar la actividad "${value.nombre}"`,
        text: 'Esta acción es irreversible!',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Si, eliminar!',
        customClass: {
          confirmButton: 'btn btn-primary',
          cancelButton: 'btn btn-outline-danger ml-1',
        },
        buttonsStyling: false,
      }).then(result => {
        if (result.value) {
          store.dispatch('actividades_taller/deleteActividad', value.id).then(() => {
            this.$swal({
              icon: 'success',
              title: 'Realizado!',
              text: 'Actividad eliminado.',
              customClass: {
                confirmButton: 'btn btn-success',
              },
            })
            store.dispatch('actividades_taller/fetchActividades').then(() => {
              if (store.state.actividades_taller.actividadesTallerDeportivo !== undefined) {
                this.totalRows = store.state.actividades_taller.actividadesTallerDeportivo.length
              }
            })
          }).catch(error => {
            console.log(error)
          })
        }
      })
    },
  },
  setup() {
    const ACTIVIDADES_TALLER_APP_STORE_MODULE_NAME = 'actividades_taller'

    // Register module
    if (!store.hasModule(ACTIVIDADES_TALLER_APP_STORE_MODULE_NAME)) store.registerModule(ACTIVIDADES_TALLER_APP_STORE_MODULE_NAME, actDeportivasTallerStoreModule)

    // UnRegister on leave
    onUnmounted(() => {
      if (store.hasModule(ACTIVIDADES_TALLER_APP_STORE_MODULE_NAME)) store.unregisterModule(ACTIVIDADES_TALLER_APP_STORE_MODULE_NAME)
    })

    const fields = [

      { key: 'nombre', label: 'Actividad', sortable: true },
      { key: 'fecha', label: 'Fecha', sortable: true },
      { key: 'acciones', label: 'acciones' },
    ]
    return {
      fields,
    }
  },
}
</script>
